// About Setcion

.bg-about {
	position: absolute;
	content: "";
	left: 0px;
	top: 0px;
	width: 50%;
	min-height: 450px;
	background: url("../images/option_g.png") no-repeat;
	background-size: cover;
  }


.about-content{
	padding: 20px 0px 0px 80px;
	h4{
		font-weight: 600;

		&:before{
			position: absolute;
			content:"\f576";
			font-family: "Font Awesome 5 Free";
			font-size: 30px;
			position: absolute;
			top: 8px;
			left: -65px;
			font-weight: 700;
		}
	}

}


